let handler;

function removeListeners() {
  if (!handler) return;
  console.log('remove Listeners');
  for (let [event, value] of Object.entries(eventMap)) {
    chrome[value.type][event].removeListener(handler[value.name]);
  }
  handler = null;
}

function addListeners() {
  if (handler) return;
  handler = new NavigationHandler();
  console.log('add Listeners');
  for (let [event, value] of Object.entries(eventMap)) {
    if (value.type !== 'webRequest') {
      chrome.webNavigation[event].addListener(
        handler[value.name]
        // value.options
      );
    } else {
      chrome.webRequest[event].addListener(
        handler[value.name],
        value.options,
        value.extras
      );
    }
  }
}

function init() {
  chrome.tabs.onActivated.addListener(function(activeInfo) {
    console.log('tab activated');
    const { tabId } = activeInfo;
    if (!isValidTab(tabId) && Object.keys(validTabs).length) {
      console.log('tab activated: invalid');
      // for (let [id, value] of Object.entries(validTabs)) {
      // if (value.handler) {
      // removeListeners(value.handler);
      removeListeners();
      // value.handler = null;
      // }
      // }
    } else if (isValidTab(tabId)) {
      console.log('tab activated: valid');
      // const currTab = validTabs[tabId];
      // for (let [id, value] of Object.entries(validTabs)) {
      //   if (value.handler) {
      //     removeListeners(value.handler);
      //     value.handler = null;
      //   }
      // }
      addListeners();
      // validTabs[tabId] = {
      //   ...currTab,
      //   handler: handler
      // };
      chrome.tabs.sendMessage(tabId, {
        message: 'reAdded'
      });
    }
  });

  chrome.tabs.onRemoved.addListener(function(tabId) {
    if (isValidTab(tabId)) {
      // removeListeners(validTabs[tabId].handler);
      removeListeners();
      validTabs[tabId] = null;
      delete validTabs[tabId];
    }
  });

  chrome.tabs.onReplaced.addListener(function(__, rtabId) {
    if (isValidTab(rtabId)) {
      // removeListeners(validTabs[rtabId].handler);
      removeListeners();
      validTabs[rtabId] = null;
      delete validTabs[rtabId];
    }
  });
}

chrome.runtime.onMessage.addListener(function(request, sender, sendResponse) {
  const { message } = request;
  if (message === 'VandalLoaded') {
    const currTab = sender.tab;
    const tabId = currTab.id;
    const matchTab = validTabs[tabId];
    const { host } = new URL(currTab.url);
    if (matchTab && matchTab.host === host) {
      matchTab.frames = [];
      matchTab.parentFrameId = null;
      sendResponse({ message: 'setupDone' });
      return;
    }
    validTabs[tabId] = { url: currTab.url, frames: [], host };
    // removeListeners(currTab && currTab.handler);

    removeListeners();
    addListeners();

    sendResponse({ message: 'setupDone' });
    console.log('setup done');
  }
});

chrome.browserAction.onClicked.addListener(function() {
  chrome.tabs.query(
    {
      active: true
    },
    tabs => {
      if (!browserActionClicked) {
        browserActionClicked = true;
        init();
      }
    }
  );
});
